body {
  background: #ffffff;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




.headerCSS {
  margin: 0px;
  display: flex;
  background: white;
  height: auto;
  line-height: 38px;
  position: relative;
}

.logocss {
  order: 0;
  width: 20%;
  font-size: 11px;
}

.navMenu {
  width: 78%;
  text-align: right;
  order: 1;
  z-index: 3;
  position: relative;
}

.navMenu ul {
  display: inline-flex;
  width: auto;
}

.navMenu li {
  margin-right: 30px;
  list-style: none;
  font-weight: lighter;
  font-size: 13px;
}

.navMenu li a {
  text-decoration: none;
  color: #454545;
}

.portadaCSS {
  height: 400px;
  margin: 0px;
  background: url(https://i.imgur.com/KGYlWeY.png);
}

.servicios {
  margin: auto;
  padding: 25px 0px;
  border-bottom: 1px solid #dcdcdc;
}

.services-flex {
  display: inline-block;
  width: 30%;
  padding: 20px 15px;
  margin: 0px;
  text-align: left;
}
.iconcss {
  background: #e43254;
  padding: 3px 10px;
  font-size: 11px;
  color: white;
  position: relative;
  text-transform: uppercase;
  font-weight: bold;
  bottom: 10px;
}
.services-flex h1 {
  margin: 0;
  font-size: 21px;
  font-weight: bold;
}
.services-flex h2 {
  margin: 0;
  font-size: 12px;
  font-weight: normal;
  color: #101010;
}

.portadaText {
  font-size: 50px;
  color: white;
  position: relative;
  font-weight: bold;
  padding-top: 40px;
  margin: auto;
  width: 70%;
}
.services, .portafolio {
  margin:auto;
  width: 86%;
  padding: 20px 0px;
  background: #ffffff;
}

.ourservices, .textoportafolio {
  text-align: left;
  padding-left: 25px;
  font-size: 30px;
  font-weight: lighter;
  color: #716b6c;
  text-transform: uppercase;
  letter-spacing: 5px;
}
.button {
  font-size: 20px;
  font-weight: bold;
  position: relative;
  background: #f34832;
  padding: 10px 15px;
  color: white;
  border-radius: 5px;
}

.informacion-portafolio {
  position: absolute;
  bottom: 1px;
  margin: auto;
  text-align: left;
  text-shadow: 1px 1px 1px black;
  color: white;
  padding: 8px 9px;
}
.categorias {
  font-size: 11px;
  display: inline-flex;
  padding: 3px 5px;
  background: #bf1939;
  margin-right: 4px;
  border-radius: 2px;
}
.informacion-portafolio h1 {
  margin: 0px;
  font-size: 25px;
}
.informacion-portafolio a {
  color: #ffffff;
  text-decoration: none;
  
}

.informacion-portafolio h2 {
  margin: 0;
  font-size: 15px;
}


.portafolioChild {
  display: inline-grid;
  width: 260px;
  height: 240px;
  background-color: #ffffff;
  background-repeat: no-repeat;
  padding: 1px;
  margin-right: 15px;
  border: 1px solid #d9d9d9;
  margin-top: 15px;
  position: relative;
}


.galeriaCSS {
  border: 1px solid #dcdcdc;
  padding: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
  display: inline-flex;
}


.textoPortafolio {
  display: block;
  text-align: left;
  padding-left: 50px;
}

.textoPortafolio h1, h2 {
  margin: 0px;
}

.textoPortafolio h2 {
  font-size: 17px;
  margin-bottom: 10px;
  font-weight: normal;
}


.TituloPagina {
  height: 100px;
  line-height: 100px;
  background: #b53f35;
  font-size: 30px;
  text-align: left;
  color: white;
  font-weight: bold;
  padding-left: 20px;
  margin-bottom: 10px;
}

.containerAbout {
  padding: 20px;
  width: 90%;
  font-size: 20px;
  text-align: left;
  margin: auto;
  margin-bottom: 20px;
}


.footer {
  background: #171414;
  padding: 15px;
  font-size: 15px;
  color: white;
  font-weight: bold;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

